.run {
  width: 100vw;
  overflow: hidden;
  position: relative;
}
.run__marquee {
  display: flex;
  width: max-content;
  animation: marquee 20s linear infinite;
  will-change: transform;
  transform: translateZ(0);
}
.run__group {
  display: flex;
}
.run__item {
  background-color: #fff;
  display: flex;
  height: 381px;
  flex-shrink: 0;
}
.run__img-box {
  flex-grow: 1;
  height: 100%;
  position: relative;
  background-color: #fff;
}
.run__img-box img {
  height: 100%;
  width: auto;
  display: block;
}
.run__video-box {
  position: absolute;
  right: 0;
  top: 0;
  width: 20%;
  height: 381px;
  z-index: 1;
}
.run__video-box video {
  width: 100%;
  height: 120%;
  object-fit: cover;
}
.run__mobile {
  height: 220px;
  position: relative;
  display: none;
}
.run__mobile video {
  width: 100%;
  height: 120%;
  object-fit: cover;
}

@media only screen and (max-width: 1023px) {
  .run__mobile {
    display: block;
  }
  .run__item {
    flex-direction: column;
  }
  .run__video-box {
    display: none;
  }
}
@keyframes marquee {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-50%);
  }
}