@use "base/mixins" as mixins;
@use "base/variables" as vars;

.run {
  width: 100vw;
  overflow: hidden;
  position: relative;

  &__marquee {
    display: flex;
    width: max-content;
    animation: marquee 20s linear infinite;
    will-change: transform;
    transform: translateZ(0);
  }

  &__group {
    display: flex;
  }

  &__item {
    background-color: #fff;
    display: flex;
    height: 381px;
    flex-shrink: 0;
  }

  &__img-box {
    flex-grow: 1;
    height: 100%;
    position: relative;
    background-color: #fff;

    img {
      height: 100%;
      width: auto;
      display: block;
    }
  }

  &__video-box {
    position: absolute;
    right: 0;
    top: 0;
    width: 20%;
    height: 381px;
    z-index: 1;

    video {
      width: 100%;
      height: 120%;
      object-fit: cover;
    }
  }

  &__mobile {
    height: 220px;
    position: relative;
    display: none;

    video {
      width: 100%;
      height: 120%;
      object-fit: cover;
    }
  }
}

@media #{vars.$small} {
  .run {
    &__mobile {
      display: block;
    }

    &__item {
      flex-direction: column;
    }

    &__video-box {
      display: none;
    }
  }
}

@keyframes marquee {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-50%);
  }
}
